body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.banner {
  padding: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;  
}

.banner Col {
  margin: 10px;
}

.banner h1 {
  /* height: 100%; 
  width:100%;
  display:flex;
  align-items: center;
  justify-content: center; */
  color: white;
}

.search-box {
  background: white;
  width: 100%;
  height: 70px;
  /* border-radius: 40px; */
  padding: 10px;
  box-shadow: 1px 5px 5px;
}

.search-txt {
  border: none;
  background: none;
  outline: none;
  float: left;
  padding: 0;
  color: black;
  font-size: 25px;
  line-height: 50px;
  width: 95%;
}


.search-btn {
  color: #8D91BA;
  float: right;
  width: 5%;
  height: 50px;
  background: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: gray;
}

::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: gray;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: gray;
}

.homeBtn {
  color: white;
  font-size: 25px;
}

.homePageBtnRow {
  width: 100%;
  margin: auto;
}

.homeBtn:hover  {
  color: #E0B47D;
  text-decoration: underline;
}

.searchAndButtons {
  width: 60%;
  margin-top: 10%;
}

.login {
  color: white;
}

.login a {
  color: black;
}

.login, .login button {
  font-size: 1.2rem;
}

.createAccount {
  text-align: center;
  display: block;
}

@media all and (min-width: 480px) {
  .login {
    padding: 60px 0;
  }

  .login form {
    margin: 0 auto;
    max-width: 320px;
  }
}
